
.header-color {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0f2027, #203a43, #2c5364); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}


.landing-grid {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0f2027, #203a43, #2c5364); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  overflow: auto; 
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100.1%;
  height: 100%;

}



.welcome-text {
  height: 100px;
  padding-top: 1.25em;
  
  text-align: center;
  font-size: 66px;
  font-weight: bold;
  color: white;
  background-color: black;
  opacity: 0.8;
  width: 55%;
  margin: auto;
  border-radius: 10px;
}

.banner-text {
  background-color: black;
  padding-top: 1em;
  padding-bottom: 1em;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  
}

.banner-text h1 {
  text-align: center;
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  text-align: center;
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 15%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;


}

.projects-grid {
  display: flex;

}